import React, { useState, useContext } from 'react'

import { SettingOutlined, CloseOutlined } from '@ant-design/icons'

import { ChatEngineContext } from 'react-chat-engine'

import ChatSettings from '../../ChatSettings'

const ChatSettingsDrawer = props => {
    const {chatSettingsIsOpen, setChatSettingsIsOpen} = props
    const context = useContext(ChatEngineContext)
    const { userCallbacks: { translate } } = context;
    const allProps = {...props, ...context.conn}

    return (
        <div className="ce-chat-settings"> 
            { 
                chatSettingsIsOpen &&
                <div style={styles.drawerContainer}>
                    <CloseOutlined
                        style={styles.closeIcon}
                        onClick={() => setChatSettingsIsOpen(false)}
                    />

                    <div style={styles.titleContainer}>
                        <div style={styles.titleText}>
                        {translate('Chat Settings', 'ChatSettingsDrawer')}
                        </div>
                    </div>

                    {
                        context.conn !== null && context.conn.renderChatSettings ?
                        context.conn.renderChatSettings(context) :
                        <ChatSettings {...allProps} />
                    }
                </div>
            }
        </div>
    );
}

export default ChatSettingsDrawer

const styles = {
    drawerContainer: { 
        position: 'absolute',
        zIndex: '1',
        top: '0px',
        left: '0px',
        width: '100%',
        height: '100%',
        backgroundColor: 'white',
        textAlign: 'left',

        display: 'flex',
        flexFlow: 'column',
    },
    closeIcon: {
        position: 'absolute',
        left: '28px',
        top: '32px'
    },
    titleContainer: {
        width: '100%',
        padding: '24px 0px',
        textAlign: 'center',
        color: 'rgb(24, 144, 255)',
    },
    titleText: {
        fontSize: '24px',
        fontWeight: '600',
    },
}
